@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;700&display=swap);
.menu{position:fixed;width:100vw;height:80px;background:linear-gradient(to bottom, rgba(45,64,89,0.8) 0%, rgba(45,64,89,0.4) 70%, transparent 100%);display:flex;align-items:center;justify-content:space-between;padding:0 30px}

.detailsMainInfo{display:flex;align-items:center;justify-content:space-evenly;flex-direction:column}.detailsMainInfo .thumbnail{width:80vw;height:80vw;box-shadow:0 0 20px 0px rgba(0,0,0,0.5)}.detailsMainInfo .drinkName{margin:32px 0;font-size:2rem;padding:16px 16px;text-align:center;font-weight:400;letter-spacing:1px;border-top:#fff solid 1px;border-bottom:#fff solid 1px}.detailsMainInfo .drinkInfo{width:80%}.detailsMainInfo .drinkInfo ul{list-style-type:none;font-size:1.5rem}.detailsMainInfo .drinkInfo ul span{font-weight:700}

.detailsBackground{background:#2d4059;min-height:100vh;color:#fff;padding:16px;padding-top:80px}

.ds-background{height:100vh;background-color:#f07b3f;flex-direction:column;display:flex;align-items:center;justify-content:space-between;padding:64px 16px;color:#fff}.ds-background a{flex-direction:column;display:flex;align-items:center;justify-content:space-between;width:100%}.ds-background .ds-name{font-size:3rem;font-weight:700;letter-spacing:-2px}.ds-background .ds-image{height:60vh;width:100%;background-position:center;background-repeat:no-repeat;background-size:contain;background-image:url(/random-drink-generator/static/media/moreDrink5.bc71f02d.png)}.ds-background .ds-randomButton{height:64px;width:60vw;font-size:2rem;font-weight:700;color:#fff;background-color:#ea5455;border:none;border-radius:3px}.ds-background .ds-randomButton:active{background-color:#e73d3e}.ds-background .ds-randomButton:hover{background-color:#ed6b6c;cursor:pointer}a{text-decoration:none;color:#fff}

.arrow{background:#fff;border-radius:99px}.arrow::after,.arrow::before{content:'';display:block;width:3px;border-radius:99px;height:12px;background:#fff;position:relative;left:36px}.arrow::after{top:-13px;-webkit-transform:rotateZ(45deg);transform:rotateZ(45deg)}.arrow::before{top:-8px;-webkit-transform:rotateZ(-45deg);transform:rotateZ(-45deg)}.arrow-thin{height:3px}.arrow-normal{height:4px}.arrow-normal::after,.arrow-normal::before{width:4px}.arrow-normal::before{top:-7px}.arrow-thick{height:5px}.arrow-thick::after,.arrow-thick::before{width:5px}.arrow-thick::before{top:-6px}.arrow-w24{width:24px}.arrow-w24::after,.arrow-w24::before{left:20px}.arrow-w32{width:32px}.arrow-w32::after,.arrow-w32::before{left:28px}.arrow-w40{width:40px}.arrow-w40::after,.arrow-w40::before{left:36px}.arrow-w48{width:48px}.arrow-w48::after,.arrow-w48::before{left:44px}.arrow-w56{width:56px}.arrow-w56::after,.arrow-w56::before{left:52px}.arrow-w64{width:64px}.arrow-w64::after,.arrow-w64::before{left:60px}.arrow-up{-webkit-transform:rotateZ(-90deg);transform:rotateZ(-90deg)}.arrow-down{-webkit-transform:rotateZ(90deg);transform:rotateZ(90deg)}.arrow-left{-webkit-transform:rotateZ(180deg);transform:rotateZ(180deg)}

.gallery{min-height:100vh;background:#2d4059;color:#fff;padding:48px 16px;padding-top:100px}.gallery .galleryHeader{padding-top:24px;font-weight:400;font-size:3rem;border-top:#fff solid 1px}.gallery .galleryInfoContainer{margin:48px 64px 48px 0;padding-top:24px;border-top:#fff solid 1px}.gallery .galleryInfoContainer .galleryInfo{margin-bottom:24px}.photosContainer{width:100%}.photosContainer .galleryPhoto{min-width:250px;max-width:400px;width:70vw;height:70vw;background-position:center;background-repeat:no-repeat;background-size:cover;margin-top:16px;float:left}.photosContainer .galleryPhoto:nth-child(2n){float:right}

.op-background{padding:128px 16px;background:#2d4059;color:#fff}.op-background .op-image{width:128px;height:256px;background-repeat:no-repeat;background-position:center;background-size:contain}.op-background .op-product{display:flex;align-items:center;justify-content:center;max-width:128px;height:auto;flex-direction:column;cursor:pointer}.op-background .op-productsContainer{display:flex;flex-wrap:wrap;justify-content:space-between}.op-background .op-text{font-size:2rem}

.imagePreview{width:72px;height:120px;border:#fff solid 2px;border-radius:3px;background:url(/random-drink-generator/static/media/moreDrink5.bc71f02d.png);background-repeat:no-repeat;background-size:96px;background-position:calc((96px - 72px) / -2) 0}

.slideshow{height:100vh;width:100%;background-image:url(/random-drink-generator/static/media/slideshow1.0c54b58e.jpg);background-repeat:no-repeat;background-position:center;background-size:cover;background-position:center;display:flex;justify-content:center;align-items:flex-end;color:#fff}.ss-bottomInfo{display:flex;padding:40px 16px;padding-top:50px;align-items:flex-end;justify-content:space-between;width:100%;background:linear-gradient(to top, rgba(45,64,89,0.8) 0%, transparent 100%)}.ss-left{display:flex;align-items:center}.ss-left .tryDrinkGroup{padding:16px}.ss-left .tryDrinkGroup .tryDrinkText{margin-bottom:16px}.goDownButton{width:32px;height:80px;border:#fff solid 2px;border-radius:3px;background:transparent;display:flex;align-items:flex-start;padding-top:20px;justify-content:center}

*{margin:0;padding:0;box-sizing:border-box;font-family:'Rubik', 'Ubuntu', sans-serif}

